				"html": "<p>If you have many options to choose from, use a <code>switch</code> statement. A <code>switch</code> statement tests a value and can have many <code>case</code> statements which defines various possible values. Statements are executed from the first matched <code>case</code> value until a <code>break</code> is encountered.</p>\n<p>Here is a <code>pseudocode</code> example:</p>\n<pre><code>switch (num) {\n  case value1:\n    statement1;\n    break;\n  case value2:\n    statement2;\n    break;\n...\n  case valueN:\n    statementN;\n    break;\n}\n</code></pre>\n<p><code>case</code> values are tested with strict equality (<code>===</code>). The <code>break</code> tells JavaScript to stop executing statements. If the <code>break</code> is omitted, the next statement will be executed.</p>",
